import React, {useState} from "react";
import './App.css';

import Main from "./components/Main";
import Footer from "./components/Footer";
import Menu from "./components/Menu";
import MenuMobile from "./components/MenuMobile";


function App() {
    const [menu, setmenu] = useState(true)
    return (

        <div className="wrapperApp">
            <div className="app__menuBottom">
                    <div className="app__menuBottom_icon" onClick={() => setmenu(!menu)}>
                        <img src={process.env.PUBLIC_URL + "/icon/white/menu.png"} width="40px" alt=""/>
                    </div>
            </div>
            {menu
                ? <div className="app__menuMobile"><MenuMobile/></div>
                : <div></div>
            }
            <div className="app__menu"><Menu/></div>
            <div className="app__main"><Main/></div>
            <div className="app__footer"><Footer/></div>


        </div>

    );
}

export default App;
