import React, {useState, useEffect} from "react";
import './home.css';
import img1 from "../img/tahta.png"
import img2 from "../img/tahta1.png"
import img3 from "../img/tahta2.png"


const Home = ({...props}) => {

    const [img, setImg] = useState(img1)
    const [timer, setTimer] = useState(0)
    useEffect(()=> {
        if (timer === 0 ) {
            setImg(img1);
            setTimeout(setTimer,5000, timer+1)

        } else if (timer === 1) {
            setImg(img2)
            setTimeout(setTimer,5000, timer+1)
        } else {
            setImg(img3)
            setTimeout(setTimer,5000, 0)
        }
    },[timer])
    var sectionStyle = {
        backgroundImage: "url(" + img + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "auto 100%",

    };

    return <>
        <div className="home__wrapper">
            <div className="home__lable">Мебельное <br/> ателье</div>

            <div className="home__animation_container">
                <div className="home__blockIMG" style={sectionStyle}></div>
                <div className="home__block1">
                    <div className="block1__lable">Melelink</div>
                    <div className="home__leaveRequest">
                        <div className="leaveRequest__text ">
                            У нас вы можете заказать ремонт, реставрацию и перетяжку любимой мягкой мебели в Москве не
                            просто
                            недорого, а по лучшим ценам!
                        </div>
                        <div className="leaveRequest__button  my_button">Оставить заявку</div>
                    </div>
                </div>
                <div className="home__block2_wrapper">
                    <div className="home__block2_conteiner">
                        <div className="home__block2__icon">
                            <img src="https://mebelink.com/uploads/images/service1.svg" alt=""/>
                        </div>
                        <div className="home__block2__text">
                            Обивка мягкой мебели
                        </div>
                    </div>
                    <div className="home__block2_conteiner">
                        <div className="home__block2__icon">
                            <img src="https://mebelink.com/uploads/images/service6.svg" alt=""/>
                        </div>
                        <div className="home__block2__text">
                            Мебель по индивидуальным проектам
                        </div>
                    </div>
                    <div className="home__block2_conteiner">
                        <div className="home__block2__icon">
                            <img src="https://mebelink.com/uploads/images/service2.svg" alt=""/>
                        </div>
                        <div className="home__block2__text">
                            Перетяжка мебели
                        </div>
                    </div>
                    <div className="home__block2_conteiner">
                        <div className="home__block2__icon">
                            <img src="https://mebelink.com/uploads/images/service4.svg" alt=""/>
                        </div>
                        <div className="home__block2__text">
                            Реставрация мебели
                        </div>
                    </div>
                    <div className="home__block2_conteiner">
                        <div className="home__block2__icon">
                            <img src="https://mebelink.com/uploads/images/service9.svg" alt=""/>
                        </div>
                        <div className="home__block2__text">
                            Работа с регионами
                        </div>
                    </div>
                    <div className="home__block2_conteiner">
                        <div className="home__block2__icon">
                            <img src="https://mebelink.com/uploads/images/service5.svg" alt=""/>
                        </div>
                        <div className="home__block2__text">
                            Рестораны, бары, кафе
                        </div>
                    </div>
                </div>
            </div>

            <div className="home__block3_wrapper">
                <div className="home__block3_lable">Наши преимущества</div>
                <div className="home__block3_icons">
                    <div className="home__block3_conteiner">
                        <div className="home__block3_icon">
                            <img src="https://mebelink.com/images/benefit_icon_1.svg" alt=""/>
                        </div>
                        <div className="home__block3_text">
                            Более 10 лет на рынке
                        </div>
                    </div>
                    <div className="home__block3_conteiner">
                        <div className="home__block3_icon">
                            <img src="https://mebelink.com/images/benefit_icon_2.svg" alt=""/>
                        </div>
                        <div className="home__block3_text">
                            Большой ассортимент материалов
                        </div>
                    </div>
                    <div className="home__block3_conteiner">
                        <div className="home__block3_icon">
                            <img src="https://mebelink.com/images/benefit_icon_3.svg" alt=""/>
                        </div>
                        <div className="home__block3_text">
                            Cпециалисты высокой квалификации
                        </div>
                    </div>
                    <div className="home__block3_conteiner">
                        <div className="home__block3_icon">
                            <img src="https://mebelink.com/images/benefit_icon_4.svg" alt=""/>
                        </div>
                        <div className="home__block3_text">
                            Гарантия 3 года
                            на все работы
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>
}
export default Home