import React from 'react';
import './menuMobile.css';
import {NavLink} from "react-router-dom";


const MenuMobile = ({...props}) => {
    return <>

        <div className="menuMobile__wrapper">

            <div className="menuMobile__conteiner">

                    <NavLink to="/">
                    <div className="menuMobile_items">
                        <div className="menuMobile_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/white/home_icon.png"} width="40px" alt=""/>
                        </div>
                    </div>
                </NavLink>
                <NavLink to="/">
                    <div className="menuMobile_items">
                        <div className="menuMobile_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/white/call_icon.png"} width="40px" alt=""/>
                        </div>
                    </div>
                </NavLink>
                <NavLink to="/">
                    <div className="menuMobile_items">
                        <div className="menuMobile_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/white/calendar_icon.png"} width="40px" alt=""/>
                        </div>
                    </div>
                </NavLink>
                <NavLink to="/">
                    <div className="menuMobile_items">
                        <div className="menuMobile_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/white/category_icon.png"} width="40px" alt=""/>
                        </div>
                    </div>
                </NavLink>
                <NavLink to="/">
                    <div className="menuMobile_items">
                        <div className="menuMobile_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/white/image_icon.png"} width="40px" alt=""/>
                        </div>
                    </div>
                </NavLink>
                <NavLink to="/">
                    <div className="menuMobile_items">
                        <div className="menuMobile_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/white/location_icon.png"} width="40px" alt=""/>
                        </div>
                    </div>
                </NavLink>
                <NavLink to="/">
                    <div className="menuMobile_items">
                        <div className="menuMobile_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/white/send_icon.png"} width="40px" alt=""/>
                        </div>
                    </div>
                </NavLink>
                <NavLink to="/">
                    <div className="menuMobile_items">
                        <div className="menuMobile_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/white/wallet_icon.png"} width="40px" alt=""/>
                        </div>
                    </div>
                </NavLink>
            </div>
        </div>

    </>
}
export default MenuMobile