import React, {useState} from "react";
import './menu.css';
import {NavLink} from "react-router-dom";


const Menu = ({...props}) => {
    const [menuActive, setMenuActive] = useState(true)
    return <>

        <div className="menu__wrapper">

            <div className="back_Logo"></div>
            <div className="menu__content">
                <NavLink to="/home">
                    <div className="menu__logo">
                        <div className="logo__img"><b>MebelLink</b></div>
                        <div className="logo__text"><b>Мебельное ателье</b><br/> На рынке с 2007 года</div>
                    </div>
                </NavLink>
                <div className="menu__contacts">
                    <div className="menu__contacts_phone">
                        <div className="phone_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/white/call_icon.png"} width="20px" alt=""/>
                        </div>
                        <div className="phone_tel">
                            <div className="tel">+7 (499) 390-42-91</div>
                            <div className="tel">+7 (926) 141-96-33</div>
                            <div className="order_a_call">Заказать звонок</div>

                        </div>
                    </div>
                    <div className="menu__operatingMode">
                        <div className="operatingMode_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/white/calendar_icon.png"} width="20px" alt=""/>
                        </div>
                        <div className="operatingMode_text">

                            Режим работы офиса: <br/>
                            ПН–ПТ: c 10:00 до 20:00<br/>
                            СБ: с 11:00 до 18:00<br/>
                            ВС: с 11:00 до 16:00<br/>
                            <br/>
                            Консультация по телефону:<br/>
                            ПН-ВС: с 09:00 до 21:00<br/>
                        </div>

                    </div>
                </div>
                <div className="menu__button my_button">Узнать цену</div>

                {menuActive
                    ?
                    <div className="menu__items">

                        <div className="operatingMode_icon_conteiner" onClick={() => {
                            setMenuActive(!menuActive)
                        }}>
                            <div className="operatingMode_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/white/category_icon.png"} width="20px"
                                     alt=""/>
                            </div>

                            <div className="operatingMode_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/white/arrow_top.png"} width="20px"
                                     alt=""/>
                            </div>
                        </div>

                        <NavLink to="/Obivka">
                            <div className="menu__item">
                                <div className="menu__item_icon">icon</div>
                                <div className="menu__item_text">
                                    Обивка мягкой мебели
                                </div>
                            </div>
                        </NavLink>

                        <NavLink to="/Peretyazhka">
                            <div className="menu__item">
                                <div className="menu__item_icon">icon</div>
                                <div className="menu__item_text">Перетяжка мягкой мебели</div>
                            </div>
                        </NavLink>

                        <NavLink to="/IndividualOrder">
                            <div className="menu__item">
                                <div className="menu__item_icon">icon</div>
                                <div className="menu__item_text">Индивидуальные проекты</div>
                            </div>
                        </NavLink>
                        <NavLink to="/CarriageScreed">
                            <div className="menu__item">
                                <div className="menu__item_icon">icon</div>
                                <div className="menu__item_text">Катерная стяжка</div>
                            </div>
                        </NavLink>
                        <NavLink to="/BarFurniture">
                            <div className="menu__item">
                                <div className="menu__item_icon">icon</div>
                                <div className="menu__item_text">Мебель для ресторанов, баров, кафе</div>
                            </div>
                        </NavLink>
                        <NavLink to="/OfficeFurniture">
                            <div className="menu__item">
                                <div className="menu__item_icon">icon</div>
                                <div className="menu__item_text">Мягкая мебель для офиса</div>
                            </div>
                        </NavLink>
                        <NavLink to="/WorkingWithOrganizations">
                            <div className="menu__item">
                                <div className="menu__item_icon">icon</div>
                                <div className="menu__item_text">Работа с организациями
                                </div>
                            </div>
                        </NavLink>


                    </div>
                    : <div className="menu__items" onClick={() => {
                        setMenuActive(!menuActive)
                    }}>
                        <div className="operatingMode_icon_conteiner">
                            <div className="operatingMode_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/white/category_icon.png"} width="20px"
                                     alt=""/>
                            </div>
                            <div className="operatingMode_text">
                                Наши услуги
                            </div>
                            <div className="operatingMode_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/white/arrow_down.png"} width="20px"
                                     alt=""/>
                            </div>
                        </div>
                    </div>}
                <div className="menu__operatingMode myCursor">
                    <div className="operatingMode_icon">
                        <img src={process.env.PUBLIC_URL + "/icon/white/image_icon.png"} width="20px" alt=""/>
                    </div>
                    <div className="operatingMode_text">

                        Портфолио
                    </div>

                </div>
                <div className="menu__operatingMode myCursor">
                    <div className="operatingMode_icon">
                        <img src={process.env.PUBLIC_URL + "/icon/white/location_icon.png"} width="20px" alt=""/>
                    </div>
                    <div className="operatingMode_text">

                        Наш адрес
                    </div>

                </div>
            </div>
        </div>

    </>
}
export default Menu