import React from 'react';
import './footer.css';
import Background from "../img/footer.jpeg";

const Footer = ({...props}) => {
    var sectionStyle = {
        backgroundImage: "url(" + Background + ")",
        backgroundRepeat: "repeat-x",
        backgroundSize: "cover",

    };
    return <>
        <div className="footer__wrapper" style={ sectionStyle }>


            <div className="footer__container">

                <div className="footer__uslugi">
                    <div className="networks_text">Услуги</div>
                    <div className="footer__uslugi_items">
                        <div className="footer__uslugi_item">Обивка мягкой мебели</div>
                        <div className="footer__uslugi_item">Перетяжка мягкой мебели</div>
                        <div className="footer__uslugi_item">Мебель по индивидуальным проектам</div>
                        <div className="footer__uslugi_item">Катерная стяжка</div>
                        <div className="footer__uslugi_item">Мебель для ресторанов, баров, кафе</div>
                        <div className="footer__uslugi_item">Мягкая мебель для офиса</div>
                        <div className="footer__uslugi_item">Работа с организациями</div>
                    </div>
                </div>
                <div className="footer__info">
                    <div className="networks_text">Информация</div>
                    <div className="footer__info_items">
                        <div className="footer__info_item">О компании</div>
                        <div className="footer__info_item">Портфолио</div>
                        <div className="footer__info_item">Расценки</div>
                        <div className="footer__info_item">Акции</div>
                        <div className="footer__info_item">Отзывы</div>

                    </div>
                </div>
                <div className="footer__contacts">
                    <div className="menu__contacts_phone">
                        <div className="phone_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/call_icon.png"} width="25px" alt=""/>
                        </div>
                        <div className="phone_tel">
                            <div className="tel">+7 (499) 390-42-91</div>
                            <div className="tel">+7 (926) 141-96-33</div>
                            <div className="order_a_call">Заказать звонок</div>

                        </div>
                    </div>
                    <div className="menu__operatingMode">
                        <div className="operatingMode_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/calendar_icon.png"} width="25px" alt=""/>
                        </div>
                        <div className="operatingMode_text">

                            Режим работы офиса: <br/>
                            ПН–ПТ: c 10:00 до 20:00<br/>
                            СБ: с 11:00 до 18:00<br/>
                            ВС: с 11:00 до 16:00<br/>
                            <br/>
                            Консультация по телефону:<br/>
                            ПН-ВС: с 09:00 до 21:00<br/>
                        </div>

                    </div>
                    <div className="menu__operatingMode">
                        <div className="operatingMode_icon">
                            <img src={process.env.PUBLIC_URL + "/icon/location_icon.png"} width="25px" alt=""/>
                        </div>
                        <div className="operatingMode_text">
                            105082, Россия, Москва, ул. Большая Почтовая, д. 40, стр. 10 (заезд и проход со стороны Рубцовской набережной)
                        </div>

                    </div>
                </div>
                <div className="footer__social">
                    <div className="networks">

                        <div className="networks_text">
                            <img src={process.env.PUBLIC_URL + "/icon/send_icon.png"} width="25px" alt=""/>
                            Мы в соцсетях
                        </div>
                        <div className="networks_icons">
                            <div className="networks_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/social/vk.png"} width="30px" alt=""/>
                            </div>
                            <div className="networks_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/social/fb.png"} width="30px" alt=""/></div>
                            <div className="networks_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/social/in.png"} width="30px" alt=""/>
                            </div>
                            <div className="networks_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/social/ok.png"} width="30px" alt=""/>
                            </div>
                        </div>
                    </div>
                    <div className="footer__text">Политика обработки персональных данных</div>
                    <div className="payment">
                        <div className="networks_text">
                            <img src={process.env.PUBLIC_URL + "/icon/wallet_icon.png"} width="25px" alt=""/>
                            Принимаем к оплате
                        </div>
                        <div className="payment_icons">
                            <div className="payment_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/social/visa.png"} height="40px" alt=""/>
                            </div>
                            <div className="payment_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/social/master_card.png"} height="40px" alt=""/>
                            </div>
                            <div className="payment_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/social/mir.png"} height="40px" alt=""/>
                            </div>
                            <div className="payment_icon">
                                <img src={process.env.PUBLIC_URL + "/icon/social/bk.png"} height="40px" alt=""/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </>
}
export default Footer