import React from 'react';
import './main.css';
import {Route, Switch} from "react-router";
import Home from "./Home";
import Obivka from "./main/Obivka";
import Peretyazhka from "./main/Peretyazhka";
import IndividualOrder from "./main/IndividualOrder";
import CarriageScreed from "./main/CarriageScreed";
import BarFurniture from "./main/BarFurniture";
import OfficeFurniture from "./main/OfficeFurniture";
import WorkingWithOrganizations from "./main/WorkingWithOrganizations";
import Background from '../img/Back.jpg';



const Main = ({menuActive, setmenuActive, ...props}) => {
    var sectionStyle = {
        backgroundImage: "url(" + Background + ")",
        backgroundRepeat: "repeat-x",

    };
    return <>

        <div className="main__wrapper" style={ sectionStyle }>
{/*

            <div className="main__back"><img src={process.env.PUBLIC_URL + "/img/Back.jpg"} width='100%' alt="dd"/></div>
*/}

            <div className="main__content" >

                <Switch>

                    {/*<Route exact path="/" component={Home}/>*/}
                    <Route path="/Home" component={Home}/>
                    <Route path="/Obivka" component={Obivka}/>
                    <Route path="/Peretyazhka" component={Peretyazhka}/>
                    <Route path="/IndividualOrder" component={IndividualOrder}/>
                    <Route path="/CarriageScreed" component={CarriageScreed}/>
                    <Route path="/BarFurniture" component={BarFurniture}/>
                    <Route path="/OfficeFurniture" component={OfficeFurniture}/>
                    <Route path="/WorkingWithOrganizations" component={WorkingWithOrganizations}/>
                </Switch>
            </div>

        </div>

    </>
}
export default Main